
import XNumber from 'vux/src/components/x-number/index.vue'

const Big = require('big.js')

export default {
name: 'inline-x-number',
props: {
blurFn: Function,
subFn: Function,
addFn: Function
},
methods: {
add () {
if (!this.disabledMax) {
const x = new Big(this.currentValue)
this.currentValue = x.plus(this.step) * 1
this.addFn(this.currentValue)
}
},
sub () {
if (!this.disabledMin) {
const x = new Big(this.currentValue)
this.currentValue = x.minus(this.step) * 1
this.subFn(this.currentValue)
}
},
blur (e) {
if (this.currentValue === '') {
this.currentValue = 0
}
this.blurFn(e, this.currentValue)
}
},
extends: XNumber
}



