
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
props: {
back: String
},
components: {
XHeader
},
methods: {
backFn () {
if(this.back){
this.$router.push(this.back)
}else{
this.$router.back(-1)
}
}
},
mounted(){
console.log(this.backPath);
},
data () {
return {
backPath:this.back
}
}
}



