export default {
  props: {
    merchantGoodsList: Array,
    goodsTypeOpt: Object,
    order: Object,
    canLink: Boolean, //   false
    goodsList: Array, // 
    showDiscount: Boolean, // 
    showPrice: Boolean, // 
    showPasteArr: Array // 
  },
  mounted () {
    if (this.goodsList) {
      this.goodsListTmp = this.goodsList.map(v => {
        v.mainImgUrl = v.mainImgUrl || '/static/goods-default.png'
        return v
      })
    }
  },
  methods: {
  },
  data () {
    return {
      goodsListTmp: undefined
    }
  }
}



