
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import http from '@/http'
import {orderStatusListOpt, goodsTypeOpt} from './js/attr'
import Vue from 'vue'
import goodsItem from './components/goodsItem'
import {wxListTpl} from './js/formatTpl'
import {headNav} from '@/components'
Vue.use(ConfirmPlugin)

export default {
components: {
TabItem, Tab, XHeader, ConfirmPlugin, Flexbox, FlexboxItem, 'good-item': goodsItem, 'head-nav':headNav
},
methods: {
checkGoodNum (merchant) {
let count = 0
merchant.merchantGoodsList.forEach((v) => {
v.wxOrderGoodsVOList.forEach((v2) => {
count++
})
})

return count
},
getList (type) {
http('ht-mj-order-server/order/wx/list', {wxOrderQueryVO: {
'currentPage': 1,
'timeRange': type,
'pageSize': 999999
}}).then((suc) => {
this.list = []
suc.dataList.map((v, i) => {
v.remarkgoodsNum = 0
v.merchantGoodsList.map((merchant, j) => {
merchant.wxOrderGoodsVOList = merchant.wxOrderGoodsVOList.map((goods, k) => {
if (goods.isRemark === 1) {
v.remarkgoodsNum += goods.goodsNum
v.goodsTotalNum = v.remarkgoodsNum
goods.mainImgUrl = goods.mainImgUrl || '/static/goods-default.png'
}
return goods
})
})
this.list.push(v)
})
})
},
receipt (orderSn) {
http('ht-mj-order-server/order/wx/receipt', {orderSn}).then((res) => {
this.getList()
})
},
keyUp (orderSn) {
this.$vux.confirm.show({
content: ' ',
onConfirm: () => {
this.receipt(orderSn)
}
})
},
gotoStore (merchantId) {
if (merchantId !== 0) {
this.$router.push(`/Search/Store/${merchantId}`)
}
}
},
mounted: function () {
this.getList(2)
},
data () {
return {
list: undefined,
orderStatusListOpt,
goodsTypeOpt
}
}
}



